  vertical-align: middle;
  width: 100%;
  margin-right: -55px;
  box-sizing: border-box; }
.el-progress-bar__outer {
    height: 6px;
    border-radius: 100px;
    background-color: #ebeef5;
    overflow: hidden;
    position: relative;
    vertical-align: middle; }
.el-progress-bar__inner {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    background-color: #014b70;
    text-align: right;
    border-radius: 100px;
    line-height: 1;
