.el-drawer__title {
    margin: 0;
    flex: 1;
    line-height: inherit;
    font-size: 1rem; }
.el-drawer__close-btn {
    border: none;
    cursor: pointer;
    font-size: 20px;
    color: inherit;
    background-color: transparent; }
.el-drawer__body {
    flex: 1; }
.el-drawer__body > * {
      box-sizing: border-box; }
.el-drawer.ltr, .el-drawer.rtl {
    height: 100%;
    top: 0;
    bottom: 0; }
.el-drawer.ttb, .el-drawer.btt {
    width: 100%;
    left: 0;
    right: 0; }
.el-drawer.ltr {
    left: 0; }
.el-drawer.rtl {
    right: 0; }
.el-drawer.ttb {
    top: 0; }
.el-drawer.btt {
    bottom: 0; }
