.v-checkbox-wrapper{
    cursor: pointer;
    font-size: 12px;
    display: inline-block;
    position: relative;
}
.v-checkbox{

    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: text-bottom;
}
.v-checkbox-checked:after {

    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 2px;
    border: 1px solid #108ee9;
    content: "";
    animation-fill-mode: both;
    visibility: hidden
}
.v-checkbox-input{
    position: absolute;
    left: 0;
    z-index: 1;
    cursor: pointer;
    opacity: 0;
    filter: alpha(opacity=0);
    top: 0;
    bottom: 0;
    right: 0;
   /* width: 100%;
    height: 100%;*/
}
.v-checkbox-inner{

    position: relative;
    top: 0;
    left: 0;
    display: block;
    width: 14px;
    height: 14px;
    border: 1px solid #abbacc;
    border-radius: 2px;
    background-color: #fff;
    transition: all .3s;
}
.v-checkbox-inner:after {
    transform: rotate(45deg) scale(0);
    position: absolute;
    left: 4px;
    top: 1px;
    display: table;
    width: 5px;
    height: 8px;
    border: 2px solid #fff;
    border-top: 0;
    border-left: 0;
    content: " ";
    transition: all .1s cubic-bezier(.71,-.46,.88,.6)
}
.v-checkbox-checked .v-checkbox-inner:after {
    transform: rotate(45deg) scale(1);
    position: absolute;
    display: table;
    border: 2px solid #fff;
    border-top: 0;
    border-left: 0;
    content: " ";
    transition: all .2s cubic-bezier(.12,.4,.29,1.46) .1s;
}
.v-checkbox-checked .v-checkbox-inner,.v-checkbox-indeterminate .v-checkbox-inner{
    background-color: #108ee9;
    border-color: #108ee9;
}
.v-checkbox-input:focus+.v-checkbox-inner,
.v-checkbox-wrapper:hover .v-checkbox-inner,
.v-checkbox:hover .v-checkbox-inner {
    border-color: #108ee9
}
.v-checkbox-disabled {
    cursor: not-allowed
}
.v-checkbox-disabled.v-checkbox-checked .v-checkbox-inner:after {
    animation-name: none;
    border-color: rgba(0,0,0,.25)
}
.v-checkbox-disabled .v-checkbox-input {
    cursor: not-allowed
}
.v-checkbox-disabled .v-checkbox-inner  {
    border-color: #d9d9d9!important;
    background-color: #f7f7f7
}
.v-checkbox-disabled .v-checkbox-inner:after {
    animation-name: none;
    border-color: #f7f7f7
}
.v-checkbox-disabled+span {
    color: rgba(0,0,0,.25);
    cursor: not-allowed
}
.v-checkbox-indeterminate .v-checkbox-inner:after {
    content: " ";
    transform: scale(1);
    position: absolute;
    left: 2px;
    top: 5px;
    width: 8px;
    height: 1px
}
.v-checkbox-indeterminate.v-checkbox-disabled .v-checkbox-inner:after {
    border-color: rgba(0,0,0,.25)
